import { lego, not } from '@armathai/lego';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { tutorialTryAnotherSequenceCompleteGuard } from '../../guards/ad/tutorial-try-another-sequence-complete-guard';
import { store } from '../../models/store';
import { nextTutorialSequenceCommand } from '../ad/tutorial/next-tutorial-sequence-command';
import { showTutorialSequenceCommand } from '../ad/tutorial/show-tutorial-sequence-command';
import { blockPlayerMemberCardCommand } from '../game/board/member/block-player-member-card-command';
import { deselectPlayerMemberCardCommand } from '../game/board/member/deselect-player-member-card-command';

export function onPlayerViewCardRejectCompleteCommand(cardUuid) {
  const card = store.game.boardModel.player.getCardByUuid(cardUuid);

  lego.command
    //
    .payload(card)
    .execute(deselectPlayerMemberCardCommand, blockPlayerMemberCardCommand)

    .guard(tutorialModelGuard, not(tutorialTryAnotherSequenceCompleteGuard))
    .execute(nextTutorialSequenceCommand, showTutorialSequenceCommand);
}
