import { CellAlign, CellScale } from '@armathai/phaser2-grid';

export function getCTAWinContentGridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta-win-content',
    cells: [
      {
        name: 'bg',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'treasure',
        align: CellAlign.LeftTop,
        bounds: { x: 0.425, y: 0.11, width: 0.7, height: 1.1 }
      },
      {
        name: 'play-button',
        bounds: { x: 0, y: 0.8, height: 0.15 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

