import { CellScale } from '@armathai/phaser2-grid';

export function getCTALoseContentGridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta-lose-content',
    cells: [
      {
        name: 'play-button',
        bounds: { x: 0, y: 0.7, height: 0.18 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

