/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCTAGridConfig } from '../../configs/grid-configs';
import { AdStatus, GameOverReasons } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import moduleBindings from '../../module-bindings';
import { CTALoseContent } from './cta-lose-content';
import { CTAWinContent } from './cta-win-content';

export class CTAView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this.bindings = moduleBindings;
    this.alpha = 0;
    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);

    this.build(this.getGridConfig());
  }

  get name() {
    return 'CTAView';
  }

  getGridConfig() {
    return getCTAGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  build(config) {
    super.build(config);
  }

  _appear() {
    const tw = this.game.add.tween(this).to({ alpha: 1 }, 500, Phaser.Easing.Cubic.InOut, true);
    tw.universal = true;

    tw.onComplete.add(() => {
      this._bg.showPlayBtn();
    });
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this._buildCta();
        break;
      case AdStatus.Game:
        this.removeChildren();
        break;

      default:
        break;
    }
  }

  _buildCta() {
    switch (store.ad.cta.reason) {
      case GameOverReasons.victory:
        this._buildWinCta();
        break;
      case GameOverReasons.Idled:
        this._buildLoseCta();
        break;
      default:
        break;
    }
    this._appear();
  }

  _buildWinCta() {
    this._bg = new CTAWinContent();
    this.setChild('cta', this._bg);
  }

  _buildLoseCta() {
    this._bg = new CTALoseContent();
    this.setChild('cta', this._bg);
  }
}
