/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCtaPlayButtonConfig } from '../../configs/button-configs';
import { getCTAWinContentGridConfig } from '../../configs/grid-configs';
import { getTreasureImageConfig } from '../../configs/image-configs';
import { getStarsEmitterConfig } from '../../configs/particles-configs';
import { InputPriority } from '../../constants';
import { ViewEvents } from '../../events/view-events';
import moduleBindings from '../../module-bindings';
import { getGameBounds, makeEmitter, makeGradient, makeImage, makePixel, pulse } from '../../utils';
import { Button } from '../../utils/button/button';

export class CTAWinContent extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._blocker = null;
    this.bindings = moduleBindings;
    this.build(this.getGridConfig());
  }

  get name() {
    return 'CTAWinContent';
  }

  getGridConfig() {
    return getCTAWinContentGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  build(config, blockerAlpha = 0) {
    super.build(config);
    this._buildBackGround();
    this._buildStarsParticle();
    this._buildTreasure();
    this._buildBtn();
    this._buildBlocker(blockerAlpha);
  }

  showPlayBtn() {
    this._playBtn.alpha = 1;
    this._playBtnTw = this.game.add
      .tween(this._playBtn)
      .from({ y: getGameBounds().bottom + this._playBtn.height }, 500, Phaser.Easing.Back.Out, true);

    this._playBtnTw.onComplete.add(() => {
      pulse(this._playBtn, 0.03);
    });
  }

  buildPlayButton(config) {
    return this._buildButton(config, this._onPlayClick);
  }

  _buildStarsParticle() {
    this._starsEmitter = makeEmitter(getStarsEmitterConfig(this));
  }

  _buildTreasure() {
    this._treasureImg = makeImage(getTreasureImageConfig());
    this.setChild('treasure', this._treasureImg);
  }

  _buildBtn() {
    this._playBtn = this.buildPlayButton(getCtaPlayButtonConfig());
    this._playBtn.alpha = 0;
    this.setChild('play-button', this._playBtn);
  }

  _buildBlocker(alpha) {
    this._blocker = makePixel({ alpha });
    this._blocker.inputEnabled = true;
    this._blocker.input.priorityID = InputPriority.Cta;
    this._blocker.name = 'CTAView';
    this.setChild('blocker', this._blocker);

    if (CI_API.Globals.PARAMS.cta_scrn_clickable) {
      this._blocker.events.onInputDown.add(this._onScreenClick, this);
    }
  }

  _buildBackGround() {
    this._ctaBg && this._ctaBg.destroy();
    this._ctaBg = makeGradient({
      width: CI_API.game.width,
      height: CI_API.game.height,
      horizontal: 0,
      vertical: 1,
      colors: [
        {
          percent: 0,
          color: '#071021'
        },
        {
          percent: 0.6,
          color: '#123a6e'
        },
        {
          percent: 1,
          color: '#01c9c9'
        }
      ]
    });
    this.setChild('bg', this._ctaBg);
  }

  _buildButton(config, callback) {
    const btn = new Button(config);
    btn.onClick.add(callback, this);
    return btn;
  }

  _onScreenClick() {
    lego.event.emit(ViewEvents.CtaView.ScreenClick);
  }

  _onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }

  _onRetryClick() {
    lego.event.emit(ViewEvents.CtaView.RetryClick);
  }
}
