import { ASSETS } from '../constants';
import { getGameBounds } from '../utils';
import { CoinFallParticle } from '../utils/emitters/coin-fall/coin-fall-particle';

const stars = 'stars';

function getStarsParticlesConfig() {
  return {
    key: stars,
    data: {
      lifespan: 3000,
      image: ASSETS,
      frame: ['fx/sparkle_1.png', 'fx/sparkle_2.png', 'fx/sparkle_3.png', 'fx/sparkle_4.png'],
      alpha: {
        initial: 0,
        value: 0.8,
        control: [
          { x: 0, y: 0 },
          { x: 0.5, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      scale: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.4 },
          { x: 0.5, y: 1 },
          { x: 1, y: 0.3 }
        ]
      },
      vy: 0,
      vx: 0,
      rotation: { delta: 1 }
    }
  };
}

export const particles = [getStarsParticlesConfig()];

export function getStarsEmitterConfig(parent) {
  const { x, y, width, height } = getGameBounds();

  return {
    key: stars,
    parent,
    x,
    y,
    force: { x: 0, y: 0 },
    config: {
      zone: CI_API.game.particleStorm.createRectangleZone(2.2 * width, height),
      total: LP(10, 7),
      repeat: -1,
      frequency: 400,
      delay: { start: 0, step: 30, visible: true }
    }
  };
}

export function getCtaCoinEmitterConfig(parent) {
  return {
    area: {
      random: true,
      shape: new Phaser.Line(-100, -100, CI_API.game.width * 2.2 + 100, -100)
    },
    repeat: true,
    count: LP(150, 80),
    parent,
    gravity: { x: 0, y: 5 },
    scale: { min: 1, max: 1.3 },
    rotation: { min: Math.PI, max: 2 * Math.PI },
    delay: { min: 0, max: 3000 },
    duration: { min: 3000, max: 3000 },
    velocity: { x: { min: 0, max: 0 }, y: { min: 300, max: 700 } },
    particleClass: CoinFallParticle
  };
}
