import { CardState, CardSuit, MemberState } from '../../../constants';
import { MemberModel } from './member-model';

export class PlayerMemberModel extends MemberModel {
  selectCard(suit) {
    this._setAllCardsState(CardState.Block);
    this._unlockAllowedCards(suit);

    this._state = MemberState.SelectCard;
  }

  turnAllowedCardsInputOff() {
    const cards = this._cards.filter(card => card.state === CardState.Unblock);
    cards.forEach(card => {
      card.input = false;
    });
  }

  turnAllowedCardsInputOn() {
    const cards = this._cards.filter(card => card.state === CardState.Unblock);
    cards.forEach(card => {
      card.input = true;
    });
  }

  deselectCard(cardUuid) {
    const card = this.getCardByUuid(cardUuid);
    card.state = CardState.Unblock;
  }

  blockCard(cardUuid) {
    const card = this.getCardByUuid(cardUuid);
    card.state = CardState.Block;
  }

  rejectCard(cardUuid) {
    const card = this.getCardByUuid(cardUuid);
    this._setAllCardsInput(false);
    card.state = CardState.Reject;
  }

  placeCard(cardUuid) {
    const card = this.getCardByUuid(cardUuid);
    this._lastPlacedCard = card;
    card.state = CardState.Accept;
    const index = this._cards.indexOf(card);
    this._cards.splice(index, 1);

    this._state = MemberState.PlaceCard;

    this._setAllCardsInput(false);
  }

  idleCards() {
    this._setAllCardsState(CardState.Unblock);
    this._setAllCardsInput(false);
  }

  highlightSpades() {
    this._setAllCardsState(CardState.Block);
    this._setAllCardsInput(false);

    const spades = this.getCardsBySuit(CardSuit.Spades);
    spades.forEach(card => {
      card.state = CardState.Unblock;
    });
  }

  highlightAllCards() {
    this._setAllCardsState(CardState.Unblock);
    this._setAllCardsInput(false);
  }

  _setAllCardsState(state) {
    this._cards.forEach(card => {
      card.state = state;
    });
  }

  _setAllCardsInput(input) {
    this._cards.forEach(card => {
      card.input = input;
    });
  }

  _unlockAllowedCards(suit) {
    const allowedCards = [];
    if (suit) {
      allowedCards.push(...this.getCardsBySuit(suit));
    } else {
      allowedCards.push(...this.getCardsBySuit(CardSuit.Hearts));
      allowedCards.push(...this.getCardsBySuit(CardSuit.Diamonds));
      allowedCards.push(...this.getCardsBySuit(CardSuit.Clubs));
    }

    if (!allowedCards.length) {
      allowedCards.push(...this.getCardsBySuit(CardSuit.Spades));
    }

    allowedCards.forEach(card => {
      card.state = CardState.Unblock;
      card.input = true;
    });
  }
}
