import { CardRank, CardSuit, TutorialSequence } from '../constants';

/**
 * Define tutorial behavior.
 * @parameter text: Content of tutorial sequence.
 *
 * @parameter duration: Milliseconds delay before tutorial sequence completes. Set -1 to make delay endless.
 *
 * @parameter clickToComplete: If set 'true', the entire screen will be clickable to complete the sequence.
 * Otherwise you should use property duration to complete it, or handle it manually.
 */
export function getTutorialConfig() {
  const params = CI_API.Globals.PARAMS;

  return [
    {
      id: TutorialSequence.SpadesTrump,
      text: 'SPADES ARE ALWAYS TRUMP!',
      duration: params.tutorial_time,
      clickToComplete: true,
      fontSize: 50,
      icon: 'spade'
    },
    {
      id: TutorialSequence.FollowSuit,
      text: 'FOLLOW THE SUIT!',
      duration: params.tutorial_time,
      clickToComplete: true,
      fontSize: 64,
      icon: 'heart',
      hand: {
        card: {
          suit: CardSuit.Hearts,
          rank: CardRank.Ace
        }
      }
    },
    {
      id: TutorialSequence.TryAnother,
      text: 'TRY ANOTHER CARD!',
      duration: params.tutorial_time,
      clickToComplete: true,
      fontSize: 64,
      hand: {
        card: {
          suit: CardSuit.Hearts,
          rank: CardRank.Ace
        }
      }
    },
    {
      id: TutorialSequence.HighestWins,
      text: 'HIGHEST CARD WINS THE TRICK!',
      duration: params.tutorial_time,
      clickToComplete: true,
      fontSize: 50,
      wordWrapWidth: LP(600, 400)
    },
    {
      id: TutorialSequence.GoLower,
      text: 'TIP: GO WITH LOWER TRUMP',
      duration: params.tutorial_time,
      clickToComplete: true,
      fontSize: 50,
      wordWrapWidth: LP(600, 400),
      hand: {
        card: {
          suit: CardSuit.Spades,
          rank: CardRank[2]
        }
      }
    }
  ];
}
