import { lego, not } from '@armathai/lego';
import { higherRankCardGuard } from '../../guards/ad/higher-rank-card-guard';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { tutorialTryAnotherSequenceCompleteGuard } from '../../guards/ad/tutorial-try-another-sequence-complete-guard';
import { cardUnblockGuard } from '../../guards/game/board/card-unblock-state-guard';
import { store } from '../../models/store';
import { placePlayerMemberCardCommand } from '../game/board/member/place-player-member-card-command';
import { rejectPlayerMemberCardCommand } from '../game/board/member/reject-player-member-card-command';

export function onPlayerViewCardClickedCommand(cardUuid) {
  const card = store.game.boardModel.player.getCardByUuid(cardUuid);

  lego.command
    // When tutorial param equal true user, should play the highest card of suit
    .payload(card)
    .guard(tutorialModelGuard, not(tutorialTryAnotherSequenceCompleteGuard), not(higherRankCardGuard), cardUnblockGuard)
    .execute(rejectPlayerMemberCardCommand)

    .payload(card)
    .guard(tutorialModelGuard, not(tutorialTryAnotherSequenceCompleteGuard), higherRankCardGuard, cardUnblockGuard)
    .execute(placePlayerMemberCardCommand)

    .payload(card)
    .guard(tutorialModelGuard, tutorialTryAnotherSequenceCompleteGuard, cardUnblockGuard)
    .execute(placePlayerMemberCardCommand)
    // ..............

    // when tutorial param equal false, user will be able to play any card he wants
    .payload(card)
    .guard(not(tutorialModelGuard), cardUnblockGuard)
    .execute(placePlayerMemberCardCommand);
}
