import { CardState, CardSuit } from '../../../constants';
import { store } from '../../../models/store';

export function spadesUnblockedGuard() {
  const unblockedCard = store.game.boardModel.player.cards.find(card => card.state === CardState.Unblock);

  return unblockedCard && unblockedCard.suit === CardSuit.Spades;
}
