import { CardRank, CardState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export class CardModel extends ObservableModel {
  constructor({ suit, key, row }, ownerUuid) {
    super('CardModel');

    this._suit = suit;
    this._key = key;
    this._row = row;
    this._rank = CardRank[key];
    this._input = undefined;
    this._ownerUuid = ownerUuid;
    this._state = CardState.Unknown;

    this.makeObservable('_state', '_input');
  }

  get suit() {
    return this._suit;
  }

  get key() {
    return this._key;
  }

  get row() {
    return this._row;
  }

  get rank() {
    return this._rank;
  }

  get ownerUuid() {
    return this._ownerUuid;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  get input() {
    return this._input;
  }

  set input(value) {
    this._input = value;
  }
}
