import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getForegroundGridLandscapeConfig() {
  const logoWidth = isSquareLikeScreen() ? 0.2 : 0.3;

  return {
    // debug: { color: 0xd95027 },
    name: 'foreground',
    cells: [
      {
        name: 'logo',
        align: CellAlign.LeftTop,
        bounds: { x: 0.025, y: 0.05, width: logoWidth, height: 0.23 }
      },
      {
        name: 'cta-win-logo',
        bounds: { x: 0.05, y: 0.1, width: 0.3825, height: 0.55 }
      },
      {
        name: 'cta-lose-logo',
        scale: CellScale.ShowAll,
        bounds: { x: 0.325, y: 0.075, width: 0.35, height: 0.5 }
      },
      {
        name: 'sound',
        align: CellAlign.RightTop,
        bounds: { x: 0.7, width: 0.27, y: 0.18, height: 0.1 }
      },
      {
        name: 'tutorial',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      }
    ]
  };
}

