/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { getBrokenTextConfig, getSpadesTextConfig } from '../../configs/text-configs';
import { GameOverReasons } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { delayRunnable, makePixel, makeText, tweenToCell } from '../../utils';
import { BoardView } from './board/board-view';
import { SpadeBroken } from './spade-broken';

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._build();
    this._boardView = null;

    lego.event.on(ModelEvents.GameModel.BoardModelUpdate, this._onBoardUpdate, this);
    lego.event.on(ModelEvents.TutorialSequenceModel.ShowUpdate, this._onTutorialSequenceShowUpdate, this);
    lego.event.on(ModelEvents.TutorialSequenceModel.CompleteUpdate, this._onTutorialSequenceCompleteUpdate, this);
    lego.event.on(ModelEvents.CtaModel.PreVisibleUpdate, this._onCtaPreVisibleUpdate, this);
    lego.event.on(ModelEvents.CtaModel.VisibleUpdate, this._onCtaVisibleUpdate, this);
  }

  get name() {
    return 'GameView';
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
    this._boardView && this._boardView.rebuild();
  }

  _build() {
    super.build(this.getGridConfig());
    this._buildBlocker();
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  _buildBlocker() {
    this._blocker = makePixel({ alpha: 0.5 });
    this._blocker.alpha = 0;
    this._blocker.visible = false;
    this.setChild('blocker', this._blocker);
  }

  _onTutorialSequenceShowUpdate() {
    this._boardView.dim();
  }

  _onTutorialSequenceCompleteUpdate() {
    this._boardView.brighten();
  }

  _showBlocker() {
    this._blocker.visible = true;
    this.game.add.tween(this._blocker).to({ alpha: 0.5 }, 300, Phaser.Easing.Cubic.InOut, true).universal = true;
  }

  _hideBlocker() {
    this._blocker.visible = false;
    this.game.add.tween(this._blocker).to({ alpha: 0 }, 300, Phaser.Easing.Cubic.InOut, true).universal = true;
  }

  // BOARD
  _onBoardUpdate(board) {
    board ? this._buildBoard() : this._destroyBoard();
  }

  _buildBoard() {
    this.setChild('board', (this._boardView = new BoardView()));
    this._blocker.bringToTop();
  }

  _destroyBoard() {
    if (store.ad.cta.reason === GameOverReasons.victory) {
      this._boardView.destroy();
      this._boardView = null;
    }
  }

  _onCtaVisibleUpdate() {
    if (this._spade) {
      this._spade.destroy();
      this._firstRowText.destroy();
      this._secondRowText.destroy();
    }
  }

  _onCtaPreVisibleUpdate() {
    this._boardView.brighten();

    if (store.ad.cta.reason === GameOverReasons.victory) {
      this._showBlocker();
      this.setChild('spade', (this._spade = new SpadeBroken()));
      this._spade.appear();
      this._showText();
    }
  }

  _showText() {
    this.setChild('first-row-from', (this._firstRowText = makeText(getSpadesTextConfig())));
    this.setChild('second-row-from', (this._secondRowText = makeText(getBrokenTextConfig())));

    delayRunnable(200, () => {
      tweenToCell(this, this._firstRowText, 'first-row-to', 700, Phaser.Easing.Back.Out);
      tweenToCell(this, this._secondRowText, 'second-row-to', 700, Phaser.Easing.Back.Out);
    });
  }
}
