/* eslint-disable max-len */
import { lego } from '@armathai/lego';
import { TutorialSequence } from '../../constants';
import { turnPlayerMemberAllowedCardsInputOnCommand } from '../game/board/member/turn-player-member-allowed-cards-input-on-command';

export function onTutorialSequenceShowCompleteCommand(index) {
  lego.command
    //
    .guard(() => index === TutorialSequence.TryAnother)
    .execute(turnPlayerMemberAllowedCardsInputOnCommand)

    .guard(() => index === TutorialSequence.FollowSuit)
    .execute(turnPlayerMemberAllowedCardsInputOnCommand);
}
