import { getLogoImageConfig, getSparkleImageConfig } from '../../configs/image-configs';
import { makeImage } from '../../utils';
import { Container } from '../../utils/container';

export class LogoView extends Container {
  constructor() {
    super();

    this._name = 'LogoView';
    this._build();
  }

  get name() {
    return this._name;
  }

  getBounds() {
    return this._logoImg.getBounds();
  }

  showSparkle() {
    this._sparkleImg.visible = true;
    this.game.add.tween(this._sparkleImg).from({ alpha: 0 }, 500, Phaser.Easing.Cubic.InOut, true).universal = true;
    this._rotateSparkle();
  }

  _rotateSparkle() {
    this.game.add
      .tween(this._sparkleImg)
      .to({ angle: 360 }, 4000, Phaser.Easing.Linear.None, true, 0, -1).universal = true;
  }

  _build() {
    this._buildSparkle();
    this._buildLogo();
  }

  _buildSparkle() {
    this._sparkleImg = makeImage(getSparkleImageConfig());
    this._sparkleImg.scale.set(LP(1, 0.7));
    this._sparkleImg.visible = false;
    this.addChild(this._sparkleImg);
  }

  _buildLogo() {
    this._logoImg = makeImage(getLogoImageConfig());
    this.addChild(this._logoImg);
  }
}
