/* eslint-disable class-methods-use-this */
import { getAvatarBgImageConfig, getAvatarImageConfig } from '../../../configs/image-configs';
import { makeImage, tweenTint } from '../../../utils';
import { Container } from '../../../utils/container';

export class MemberComponent extends Container {
  constructor(uuid, config) {
    super();

    this._uuid = uuid;
    this._config = config;
    this._build();
  }

  get uuid() {
    return this._uuid;
  }

  get config() {
    return this._config;
  }

  rebuild() {
    const scale = this.parent.scale.x;
    this.scale.set(LP(1.2, 1) / scale);
  }

  dim() {
    this.children.forEach(child => tweenTint(child, 0x777777, 0xffffff, 500));
  }

  brighten() {
    this.children.forEach(child => tweenTint(child, 0xffffff, 0x777777, 500));
  }

  _build() {
    this._buildBg();
    this._buildAvatar();
  }

  _buildBg() {
    const img = makeImage(getAvatarBgImageConfig());
    this.addChild((this._avatarBg = img));
  }

  _buildAvatar() {
    const { avatar: key } = this._config;

    const img = makeImage(getAvatarImageConfig(key));
    img.position.set(0, -(this._avatarBg.height - img.height) / 2);
    this.addChild(img);
  }
}
