import { InputPriority } from '../constants';
import {
  getCtaKeepPlayingButtonPatchConfig,
  getCtaPlayButtonPatchConfig,
  getPersistentCtaButtonPatchConfig
} from './ninepatch-configs';
import {
  getCtaKeepPlayingButtonTextConfig,
  getCtaPlayButtonTextConfig,
  getPersistentCtaTextConfig
} from './text-configs';

// PERSISTENT CTA
export function getPersistentCtaButtonConfig() {
  return {
    input: {
      name: 'persistent_cta_button',
      priority: InputPriority.Game
    },
    states: {
      up: {
        bg: getPersistentCtaButtonPatchConfig(),
        label: getPersistentCtaTextConfig()
      }
    }
  };
}

// CTA
export function getCtaPlayButtonConfig() {
  return {
    input: {
      name: 'cta_play_button',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getCtaPlayButtonPatchConfig(),
        label: getCtaPlayButtonTextConfig()
      }
    }
  };
}

export function getCtaKeepPlayingButtonConfig() {
  return {
    input: {
      name: 'cta_play_button',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getCtaKeepPlayingButtonPatchConfig(),
        label: getCtaKeepPlayingButtonTextConfig()
      }
    }
  };
}
