/* eslint-disable class-methods-use-this */
import { getCardImageConfig } from '../../../configs/image-configs';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class CardComponent extends Container {
  constructor(uuid, suit, key, row) {
    super();

    this.onCardClicked = new Phaser.Signal();
    this._uuid = uuid;
    this._row = row;
    this._build(suit, key);
  }

  get uuid() {
    return this._uuid;
  }

  get row() {
    return this._row;
  }

  switchInput(input) {
    this._cardImg.events.onInputDown.removeAll();
    this._cardImg.inputEnabled = true;
    this._cardImg.name = 'Card';

    if (input) {
      this._cardImg.events.onInputDown.add(this._onCardClick, this);
    }
  }

  lock() {
    this._cardImg.tint = 0x555555;
  }

  unlock() {
    this._cardImg.tint = 0xffffff;
  }

  _onCardClick() {
    this.onCardClicked.dispatch(this._uuid);
  }

  _build(suit, key) {
    const img = makeImage(getCardImageConfig(suit, key));
    this.addChild((this._cardImg = img));
  }
}
