import { BoardState, CardSuit } from '../../../constants';
import { ObservableModel } from '../../observable-model';
import { MemberModel } from './member-model';
import { PlayerMemberModel } from './player-member-model';

export class BoardModel extends ObservableModel {
  constructor() {
    super('BoardModel');

    this._state = BoardState.Unknown;
    this._members = null;
    this._currentTurnMemberIndex = null;
    this._roundWinnerUuid = null;
    this._placedCards = [];

    this.makeObservable('_state', '_members');
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  get members() {
    return this._members;
  }

  get currentTurnMemberIndex() {
    return this._currentTurnMemberIndex;
  }

  get currentTurnMember() {
    return this._members[this._currentTurnMemberIndex];
  }

  get roundWinnerUuid() {
    return this._roundWinnerUuid;
  }

  get placedCards() {
    return this._placedCards;
  }

  get opponents() {
    return this._members.filter(member => !member.isPlayer);
  }

  get player() {
    return this._members.find(member => member.isPlayer);
  }

  getMemberByUuid(uuid) {
    return this._members.find(member => member.uuid === uuid);
  }

  calculateWinner() {
    const spadesPlaced = this._placedCards.filter(card => card.suit === CardSuit.Spades);
    let winCard = null;

    if (!spadesPlaced.length) {
      winCard = this.getHighestCard(this._placedCards);
    } else {
      winCard = this.getHighestCard(spadesPlaced);
    }

    const winner = this.getMemberByUuid(winCard.ownerUuid);
    this._currentTurnMemberIndex = this._members.indexOf(winner);
    this._roundWinnerUuid = winCard.ownerUuid;
  }

  // eslint-disable-next-line class-methods-use-this
  getHighestCard(cards) {
    return cards.reduce((acc, card) => {
      return acc.rank >= card.rank ? acc : card;
    });
  }

  updateCurrentTurnMemberIndex() {
    this._currentTurnMemberIndex =
      this._currentTurnMemberIndex + 1 === this._members.length ? 0 : this._currentTurnMemberIndex + 1;
  }

  startNextTurn() {
    const suit = this._placedCards[0] ? this._placedCards[0].suit : null;
    this.currentTurnMember.selectCard(suit);
  }

  placeCard() {
    const { lastPlacedCard } = this.currentTurnMember;
    this._placedCards.push(lastPlacedCard);
    this._state = BoardState.Place;
  }

  emptyPlacedCards() {
    this._placedCards.length = 0;
    this._state = BoardState.Empty;
  }

  initialize(config) {
    const { members, firstTurnMemberIndex } = config;

    this._members = members.map(member => (member.isPlayer ? new PlayerMemberModel(member) : new MemberModel(member)));
    this._initCards(members);
    this._currentTurnMemberIndex = firstTurnMemberIndex;
  }

  _initCards(membersConfig) {
    this._members.forEach((member, index) => {
      member.initialize(membersConfig[index].cards);
    });
  }
}
