/* eslint-disable max-len */
import { lego, not } from '@armathai/lego';
import { TutorialSequence } from '../../../constants';
import { hintModelGuard } from '../../../guards/ad/hint-model-guard';
import { persistentCtaParamGuard } from '../../../guards/ad/persistent-cta-param-guard';
import { tutorialGoLowerSequenceShownGuard } from '../../../guards/ad/tutorial-go-lower-sequence-shown-guard';
import { playerMemberSelectCardStateGuard } from '../../../guards/game/board/member/player-member-select-card-state-guard';
import { store } from '../../../models/store';
import { highlightAllCardsCommand } from '../../game/board/member/highlight-all-cards-command';
import { startGamePlayCommand } from '../../game/start-game-play-command';
import { startHintVisibilityTimerCommand } from '../hint/start-hint-visibility-timer-command';
import { initializePersistentCtaModelCommand } from '../pcta/initialize-persistent-cta-model-command';
import { completeTutorialTryAnotherSequenceCommand } from './complete-tutorial-try-another-sequence-command';

export function onTutorialSequenceCompleteCommand(value, prevValue, uuid) {
  const { index } = store.ad.tutorial.getSequenceByUuid(uuid);

  lego.command
    //
    .guard(() => index === TutorialSequence.SpadesTrump, persistentCtaParamGuard)
    .execute(initializePersistentCtaModelCommand)

    .guard(() => index === TutorialSequence.SpadesTrump)
    .execute(startGamePlayCommand, highlightAllCardsCommand)

    .guard(() => index === TutorialSequence.HighestWins)
    .execute(completeTutorialTryAnotherSequenceCommand)

    .guard(
      () => index === TutorialSequence.FollowSuit || index === TutorialSequence.HighestWins,
      hintModelGuard,
      playerMemberSelectCardStateGuard,
      not(tutorialGoLowerSequenceShownGuard)
    )
    .execute(startHintVisibilityTimerCommand);
}
