import { lego } from '@armathai/lego';
import { CardState, TutorialSequence } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { getViewByProperty, searchAtlasByFrame } from '../../utils';
import { last } from '../../utils/array/last';
import { sample } from '../../utils/array/sample';

export class HintView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hand.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0.55, 0.1);

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this._hide();
  }

  destroy() {
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    const { game, ad } = store;
    const { player } = game.boardModel;
    const { tutorial } = ad;
    const cards = player.getCardsByState(CardState.Unblock);

    const { uuid } =
      tutorial && tutorial.sequences[TutorialSequence.HighestWins].complete ? sample(cards) : last(cards);
    const playerView = getViewByProperty('name', 'PlayerView');
    const { position, parent, width } = playerView.getCardByUuid(uuid);
    const { x, y } = this.parent.toLocal(position, parent);
    this.position.set(x - width / 3, y);

    this.scale.set(1);
    this.visible = true;
    this.game.tweens.removeFrom(this.scale);
    this.game.add.tween(this.scale).to({ x: 0.8, y: 0.8 }, 500, Phaser.Easing.Cubic.Out, true, 0, -1, true);
  }

  _hide() {
    this.game.tweens.removeFrom(this.scale);
    this.visible = false;
  }
}
