/* eslint-disable max-len */
import { lego, not } from '@armathai/lego';
import { BoardState } from '../../constants';
import { tutorialHighestWinsSequenceCompleteGuard } from '../../guards/ad/tutorial-highest-wins-sequence-complete-guard';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { allCardsPlacedGuard } from '../../guards/game/board/all-cards-placed-guard';
import { boardPlaceStateGuard } from '../../guards/game/board/board-place-state-guard';
import { playerThrowAceOfHeartGuard } from '../../guards/game/board/member/player-throw-ace-of-heart-guard';
import { spadesThrownGuard } from '../../guards/game/board/spades-thrown-guard';
import { itemsToCtaCompleteCommand } from '../ad/cta/items-to-cta-complete-command';
import { showTutorialHighestWinsSequenceCommand } from '../ad/tutorial/show-tutorial-highest-wins-sequence-command';
import { calculateWinnerCommand } from '../game/board/calculate-winner-command';
import { emptyPlacedCardsCommand } from '../game/board/empty-placed-cards-command';
import { idlePlayerMemberCardsCommand } from '../game/board/member/idle-player-member-cards-command';
import { setBoardStateCommand } from '../game/board/set-board-state-command';
import { updateCurrentTurnMemberIndexCommand } from '../game/board/update-current-turn-member-index-command';

export function onCardThrowCompleteCommand(isPlayerCard, cardUuid) {
  lego.command
    //

    .guard(spadesThrownGuard)
    .execute(itemsToCtaCompleteCommand)

    .guard(not(allCardsPlacedGuard), not(spadesThrownGuard))
    .payload(BoardState.Select)
    .execute(updateCurrentTurnMemberIndexCommand, setBoardStateCommand)

    .guard(() => isPlayerCard, not(spadesThrownGuard))
    .execute(idlePlayerMemberCardsCommand)

    .guard(boardPlaceStateGuard, allCardsPlacedGuard, not(spadesThrownGuard), not(tutorialModelGuard))
    .execute(calculateWinnerCommand, emptyPlacedCardsCommand)

    .guard(
      boardPlaceStateGuard,
      allCardsPlacedGuard,
      not(spadesThrownGuard),
      tutorialModelGuard,
      tutorialHighestWinsSequenceCompleteGuard
    )
    .execute(calculateWinnerCommand, emptyPlacedCardsCommand)

    .payload(cardUuid)
    .guard(
      boardPlaceStateGuard,
      allCardsPlacedGuard,
      not(spadesThrownGuard),
      tutorialModelGuard,
      not(tutorialHighestWinsSequenceCompleteGuard),
      playerThrowAceOfHeartGuard
    )
    .execute(showTutorialHighestWinsSequenceCommand);
}
