/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getTutorialSequenceGridConfig } from '../../configs/grid-configs';
import { getHandImageConfig, getTutorialIconImageConfig } from '../../configs/image-configs';
import { getTutorialTextConfig } from '../../configs/text-configs';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { getViewByProperty, makeImage, makeText } from '../../utils';

export class TutorialSequenceView extends Phaser2Grid {
  constructor(config, index) {
    super(CI_API.game);
    this._config = config;
    this._index = index;
    this._showTween = null;
    this._hideTween = null;

    lego.event.on(ViewEvents.Game.Resize, this._onResize, this);
    this.build();

    this.alpha = 0;
  }

  get index() {
    return this._index;
  }

  getGridConfig() {
    return getTutorialSequenceGridConfig();
  }

  _onResize() {
    this._hand && this._destroyHand();
  }

  build() {
    super.build(this.getGridConfig());

    const { text, fontSize, wordWrapWidth, icon, hand } = this._config;
    icon && this._buildIcons(icon);
    hand && this._buildHand();
    this.setChild(this._index, this._buildLabel(text, fontSize, wordWrapWidth));
  }

  destroy() {
    this.game.tweens.remove(this._showTween);
    this.game.tweens.remove(this._hideTween);

    super.destroy();
  }

  show() {
    this._showTween = this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Linear.None, true);
    this._showTween.universal = true;

    return this._showTween;
  }

  hide() {
    this._hideTween = this.game.add.tween(this).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);
    this._hideTween.universal = true;

    return this._hideTween;
  }

  _buildIcons(iconFrame) {
    const left = makeImage(getTutorialIconImageConfig(iconFrame));
    const right = makeImage(getTutorialIconImageConfig(iconFrame));
    this.setChild('left', left);
    this.setChild('right', right);
  }

  _buildHand() {
    const { suit, rank } = this._config.hand.card;
    const card = store.game.boardModel.player.getCardBySuitAndRank(suit, rank);

    if (card) {
      const hand = makeImage(getHandImageConfig());
      this.addChild((this._hand = hand));

      this._updateHandPosition(card);

      const tw = this.game.add.tween(this._hand).from({ alpha: 0 }, 300, Phaser.Easing.Cubic.InOut, true);
      tw.onComplete.add(() => {
        this.game.tweens.removeFrom(this._hand);
        this.game.add.tween(this._hand.scale).to({ x: 0.8, y: 0.8 }, 500, Phaser.Easing.Cubic.Out, true, 0, -1, true);
      });

      tw.universal = true;
    }
  }

  _destroyHand() {
    this._hand.destroy();
    this._hand = null;
  }

  _updateHandPosition(card) {
    if (this._hand) {
      const { uuid } = card;
      const playerView = getViewByProperty('name', 'PlayerView');
      const { position, parent, width } = playerView.getCardByUuid(uuid);
      const { x, y } = this.toLocal(position, parent);

      this._hand.position.set(x - width / 3, y);
    }
  }

  _buildLabel(text, fontSize, wordWrapWidth) {
    const label = makeText(getTutorialTextConfig(text, fontSize, wordWrapWidth));
    label.lineSpacing = -12;
    return label;
  }
}
