/* eslint-disable max-len */
import { lego, not } from '@armathai/lego';
import { tutorialFollowSuitSequenceCompleteGuard } from '../../../guards/ad/tutorial-follow-suit-sequence-complete-guard';
import { tutorialFollowSuitSequenceShownGuard } from '../../../guards/ad/tutorial-follow-suit-sequence-shown-guard';
import { tutorialModelGuard } from '../../../guards/ad/tutorial-model-guard';
import { boardSelectStateGuard } from '../../../guards/game/board/board-select-state-guard';
import { store } from '../../../models/store';
import { turnPlayerMemberAllowedCardsInputOffCommand } from './member/turn-player-member-allowed-cards-input-off-command';
import { startNextTurnCommand } from './start-next-turn-command';

export function onBoardStateUpdateCommand() {
  lego.command
    //
    .guard(boardSelectStateGuard)
    .execute(startNextTurnCommand)

    .guard(
      tutorialModelGuard,
      tutorialFollowSuitSequenceShownGuard,
      not(tutorialFollowSuitSequenceCompleteGuard),
      () => store.game.boardModel.currentTurnMember.isPlayer
    )
    .execute(turnPlayerMemberAllowedCardsInputOffCommand);
}
