export function getBackgroundViewImageConfig(key) {
  return {
    frame: key
  };
}

export function getLogoImageConfig() {
  return {
    frame: 'ui/logo.png'
  };
}

export function getSparkleImageConfig() {
  return {
    frame: 'ui/sparkle.png'
  };
}

export function getTableImageConfig() {
  return {
    frame: 'board/table.png'
  };
}

export function getSpadeCleanImageConfig() {
  return {
    frame: 'cta/spade_clean.png'
  };
}

export function getSpadeBrokenImageConfig() {
  return {
    frame: 'cta/spade_broken.png',
    x: 5,
    y: -18
  };
}

export function getAvatarBgImageConfig() {
  return {
    frame: 'member/avatar_bg.png',
    anchor: { x: 0.5, y: 1 }
  };
}

export function getAvatarImageConfig(key) {
  return {
    frame: `avatars/${key}.png`,
    anchor: { x: 0.5, y: 1 }
  };
}

export function getCardImageConfig(suit, key) {
  return {
    frame: `cards/${suit}_${key}.png`
  };
}

export function getTreasureImageConfig() {
  return {
    frame: 'cta/treasure.png'
  };
}

export function getTutorialIconImageConfig(key) {
  return {
    frame: `ui/${key}-icon.png`
  };
}

export function getHandImageConfig() {
  return {
    frame: `ui/hand.png`,
    anchor: { x: 0.55, y: 0.1 }
  };
}
