import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { getGameBounds } from '../../utils';

export function getGameGridLandscapeConfig() {
  const { width, height } = getGameBounds();
  const spadesBrokenScale = 1.5;
  const spadeBrokenWidth = (239 * spadesBrokenScale) / width;
  const spadeBrokenHeight = (232 * spadesBrokenScale) / height;
  const spadeBrokenX = (1 - spadeBrokenWidth) / 2;
  const spadeBrokenY = 0.15;

  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'board',
        scale: CellScale.ShowAll,
        bounds: { x: 0, y: 0, width: 1, height: 1.2 }
      },
      {
        name: 'spade',
        // debug: { color: 0x2fc900 },
        scale: CellScale.ShowAll,
        bounds: { x: spadeBrokenX, y: spadeBrokenY, width: spadeBrokenWidth, height: spadeBrokenHeight },
        cells: [
          {
            name: 'first-row-to',
            bounds: { x: 0, y: 0.3, width: 1, height: 0.2 }
          },
          {
            name: 'second-row-to',
            bounds: { x: 0, y: 0.5, width: 1, height: 0.2 }
          }
        ]
      },
      {
        name: 'first-row-from',
        bounds: {
          x: -1 * spadeBrokenWidth,
          y: spadeBrokenY + 0.3 * spadeBrokenHeight,
          width: 1 * spadeBrokenWidth,
          height: 0.2 * spadeBrokenHeight
        }
      },
      {
        name: 'second-row-from',
        bounds: {
          x: 1,
          y: spadeBrokenY + 0.5 * spadeBrokenHeight,
          width: 1 * spadeBrokenWidth,
          height: 0.2 * spadeBrokenHeight
        }
      },
      {
        name: 'blocker',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Envelop
      }
    ]
  };
}

export function getGameGridPortraitConfig() {
  const { width, height } = getGameBounds();
  const spadesBrokenScale = 1.4;
  const spadeBrokenWidth = (239 * spadesBrokenScale) / width;
  const spadeBrokenHeight = (232 * spadesBrokenScale) / height;
  const spadeBrokenX = (1 - spadeBrokenWidth) / 2;
  const spadeBrokenY = 0.3;

  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'board',
        bounds: { x: -0.12, y: 0.22, width: 1.24, height: 0.62 },
        scale: CellScale.ShowAll,
        align: CellAlign.Center,
        padding: { x: -0.05, width: 1.1 }
      },
      {
        name: 'spade',
        align: CellAlign.CenterTop,
        scale: CellScale.ShowAll,
        bounds: { x: spadeBrokenX, y: spadeBrokenY, width: spadeBrokenWidth, height: spadeBrokenHeight },
        cells: [
          {
            name: 'first-row-to',
            bounds: { x: 0, y: 0.3, width: 1, height: 0.2 }
          },
          {
            name: 'second-row-to',
            bounds: { x: 0, y: 0.5, width: 1, height: 0.2 }
          }
        ]
      },
      {
        name: 'first-row-from',
        bounds: {
          x: -1 * spadeBrokenWidth,
          y: spadeBrokenY + 0.3 * spadeBrokenHeight,
          width: 1 * spadeBrokenWidth,
          height: 0.2 * spadeBrokenHeight
        }
      },
      {
        name: 'second-row-from',
        bounds: {
          x: 1,
          y: spadeBrokenY + 0.5 * spadeBrokenHeight,
          width: 1 * spadeBrokenWidth,
          height: 0.2 * spadeBrokenHeight
        }
      },
      {
        name: 'blocker',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Envelop
      }
    ]
  };
}
