import { lego, not } from '@armathai/lego';
import { hintModelGuard } from '../guards/ad/hint-model-guard';
import { tutorialCurrentSequenceActiveGuard } from '../guards/ad/tutorial-current-sequence-active-guard';
import { tutorialFollowSuitSequenceCompleteGuard } from '../guards/ad/tutorial-follow-suit-sequence-complete-guard';
import { tutorialGoLowerSequenceShownGuard } from '../guards/ad/tutorial-go-lower-sequence-shown-guard';
import { tutorialModelGuard } from '../guards/ad/tutorial-model-guard';
import { playerMemberSelectCardStateGuard } from '../guards/game/board/member/player-member-select-card-state-guard';
import { hideHintCommand } from './ad/hint/hide-hint-command';
import { startHintVisibilityTimerCommand } from './ad/hint/start-hint-visibility-timer-command';
import { stopHintVisibilityTimerCommand } from './ad/hint/stop-hint-visibility-timer-command';

export function resizeCommand() {
  lego.command
    //
    .guard(hintModelGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand)

    .guard(hintModelGuard, not(tutorialModelGuard), playerMemberSelectCardStateGuard)
    .execute(startHintVisibilityTimerCommand)

    .guard(
      hintModelGuard,
      playerMemberSelectCardStateGuard,
      tutorialModelGuard,
      not(tutorialCurrentSequenceActiveGuard),
      tutorialFollowSuitSequenceCompleteGuard,
      not(tutorialGoLowerSequenceShownGuard)
    )
    .execute(startHintVisibilityTimerCommand);
}
