import { FONT1 } from '../constants';

// TUTORIAL
export function getTutorialTextConfig(text, fontSize, wordWrapWidth = 300) {
  return {
    text,
    style: {
      font: FONT1,
      fontSize: fontSize + 11,
      fill: '#ffffff',
      align: 'center',
      wordWrap: true,
      wordWrapWidth,
      stroke: '#000000',
      strokeThickness: 7
    }
  };
}

