import { ASSETS } from '../constants';

export const Animations = {
  Coin: {
    Fall: 'fall'
  }
};

export function getCoinFallAnimationConfig() {
  return {
    key: ASSETS,
    data: [
      {
        name: Animations.Coin.Fall,
        prefix: 'animation/coins/coin_',
        start: 1,
        stop: 10,
        suffix: '.png',
        zeroPad: 0,
        frameRate: 40,
        loop: true
      }
    ]
  };
}
