import { getSpadeBrokenImageConfig, getSpadeCleanImageConfig } from '../../configs/image-configs';
import { makeImage } from '../../utils';
import { Container } from '../../utils/container';

export class SpadeBroken extends Container {
  constructor() {
    super();
    this._build();
  }

  appear() {
    this._container.scale.set(0);
    this._container.alpha = 1;
    this.game.add.tween(this._container.scale).to({ x: 1, y: 1 }, 400, Phaser.Easing.Back.Out, true).universal = true;

    this.game.add
      .tween(this._spadeImg.scale)
      .to({ x: 1.4, y: 0.9 }, 200, Phaser.Easing.Back.Out, true, 200, 0, true).universal = true;

    const tw = this.game.add
      .tween(this._crackImg.scale)
      .to({ x: 1.4, y: 0.9 }, 200, Phaser.Easing.Back.Out, true, 200, 0, true);

    tw.universal = true;
    tw.onStart.add(() => {
      this._crackImg.visible = true;
    });
  }

  _build() {
    this._buildSpade();
  }

  _buildSpade() {
    this._container = new Container();

    this._spadeImg = makeImage(getSpadeCleanImageConfig());
    this._crackImg = makeImage(getSpadeBrokenImageConfig());
    this._crackImg.visible = false;

    this._container.addChild(this._spadeImg);
    this._container.addChild(this._crackImg);
    this._container.alpha = 0;
    this.addChild(this._container);
  }
}
