/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getUIGridConfig } from '../../configs/grid-configs';
import { ModelEvents } from '../../events/model-events';
import { getGameBounds } from '../../utils';
import { PersistentCTAView } from './persistent-cta-view';

export class UIView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event.on(ModelEvents.AdModel.PersistentCtaUpdate, this._onPersistentCtaUpdate, this);
  }

  get name() {
    return 'UIView';
  }

  getGridConfig() {
    return getUIGridConfig();
  }

  // PERSISTENT
  _onPersistentCtaUpdate(persistentCta) {
    persistentCta ? this._buildPersistentCta() : this._destroyPersistentCta();
  }

  _buildPersistentCta() {
    this.setChild('p_cta', (this._persistentCtaView = new PersistentCTAView()));
    this._showPersistentCta();
  }

  _showPersistentCta() {
    this._persistentCtaView.alpha = 1;

    const { x, y } = this._persistentCtaView;
    const yPos = LP(y, getGameBounds().bottom + this._persistentCtaView.height);
    const xPos = LP(getGameBounds().right, x);
    this._persistentCtaViewTw = this.game.add
      .tween(this._persistentCtaView)
      .from({ x: xPos, y: yPos }, 500, Phaser.Easing.Back.Out, true);
  }

  _destroyPersistentCta() {
    this._persistentCtaView.destroy();
  }
}
