/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getMemberConfigByName } from '../../../configs/board-config';
import { getTableImageConfig } from '../../../configs/image-configs';
import { BoardState, CardSuit } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { makeImage, tweenTint } from '../../../utils';
import { Container } from '../../../utils/container';
import { CardComponent } from './card-component';
import { MemberComponent } from './member-component';
import { PlayerView } from './player-view';

export class BoardView extends Container {
  constructor() {
    super();
    this._build();

    this._player = null;
    this._opponents = null;
    this._placedCards = [];

    lego.event.on(ModelEvents.BoardModel.MembersUpdate, this._onMembersUpdate, this);
    lego.event.once(ModelEvents.MemberModel.CardsUpdate, this._onCardsUpdate, this);
    lego.event.on(ModelEvents.BoardModel.StateUpdate, this._onBoardStateUpdate, this);
  }

  get name() {
    return 'BoardView';
  }

  getBounds() {
    const topOffset = LP(100, 124);
    const bottomOffset = LP(-30, 0);
    const { width, height: tH } = this._table;
    const height = tH + bottomOffset + topOffset;

    return new Phaser.Rectangle(-width / 2, -tH / 2 - topOffset, width, height);
  }

  getMemberByUuid(uuid) {
    const members = [...this._opponents, this._player];

    return members.find(member => member.uuid === uuid);
  }

  rebuild() {
    this._opponents.forEach(opponent => {
      opponent.rebuild();

      const config = getMemberConfigByName(opponent.config.name);
      const { x, y } = config.viewConfig.position;
      opponent.position.set(x, y);
    });

    this._player.rebuild();

    const config = getMemberConfigByName('Player');
    const { x, y } = config.viewConfig.position;
    this._player.position.set(x, y);
    this._placedCardsContainer.scale.set(LP(1, 1.25));
    this._placedCardsContainer.position.set(0, LP(-130, -100));
  }

  dim() {
    this._opponents.forEach(opponent => {
      opponent.dim();
    });
    tweenTint(this._table, 0x777777, 0xffffff, 500);
  }

  brighten() {
    this._opponents.forEach(opponent => {
      opponent.brighten();
    });
    tweenTint(this._table, 0xffffff, 0x777777, 500);
  }

  _onBoardStateUpdate(state) {
    switch (state) {
      case BoardState.Place:
        this._placeCard();
        break;
      case BoardState.Empty:
        this._emptyCard();
        break;

      default:
        break;
    }
  }

  _onCardsUpdate(cards, prevValue, uuid) {
    if (uuid === this._player.uuid) {
      this._player.createCards(cards);
    }
  }

  _onMembersUpdate() {
    const { opponents, player } = store.game.boardModel;
    this._opponents = this._createOpponents(opponents);
    this._player = this._createPlayer(player);
    this.sendToBack(this._player);
    this._table.sendToBack();
  }

  _onPlayerThrowCard(cardView, suit) {
    const {
      position,
      parent,
      scale: { x: cardScale }
    } = cardView;
    const { x, y } = this._placedCardsContainer.toLocal(position, parent);
    this._placedCardsContainer.addChild(cardView);
    this._placedCards.push(cardView);
    cardView.scale.set(cardScale * parent.scale.x);
    cardView.position.set(x, y);

    const {
      viewConfig: { angle, placePosition }
    } = getMemberConfigByName(store.game.boardModel.player.name);

    if (suit === CardSuit.Spades) {
      this.bringToTop(this._placedCardsContainer);
      this._startDramaticEffect(cardView, angle, placePosition);
    } else {
      this._throwCard(cardView, angle, placePosition, true);
    }
  }

  _startDramaticEffect(cardView, angle, position) {
    const { x: endX, y: endY } = position;
    const { x: startX, y: startY } = cardView;

    this.game.add
      .tween(cardView)
      .to(
        { angle: angle - 30, x: (endX - startX) / 4, y: (endY - startY) / 4 },
        1000,
        Phaser.Easing.Sinusoidal.InOut,
        true
      );
    const tw = this.game.add.tween(cardView.scale).to({ x: 1.5, y: 1.5 }, 1000, Phaser.Easing.Sinusoidal.InOut, true);
    tw.universal = true;
    tw.onComplete.add(() => {
      this._throwCard(cardView, angle, position, true);
    });
  }

  _emptyCard() {
    const { boardModel } = store.game;
    const { roundWinnerUuid } = boardModel;
    const { name } = boardModel.getMemberByUuid(roundWinnerUuid);
    const { position } = getMemberConfigByName(name).viewConfig;

    // @ts-ignore
    const pos = this._placedCardsContainer.toLocal(position, this);

    this._startGainCardsTweens(pos);
  }

  _startGainCardsTweens(position) {
    const { x, y } = position;
    let lastTween;

    this._placedCards.forEach((card, index) => {
      lastTween = this.game.add.tween(card).to({ x, y, alpha: 0 }, 400, Phaser.Easing.Sinusoidal.Out, true, 30 * index);

      lastTween.universal = true;
      lastTween.onComplete.add(() => {
        card.destroy();
      });
    });

    // @ts-ignore
    lastTween.onComplete.add(() => {
      lego.event.emit(ViewEvents.BoardView.CardsGainComplete);
    });
  }

  _placeCard() {
    const { lastPlacedCard: cardModel, uuid, isPlayer } = store.game.boardModel.currentTurnMember;
    const { position } = this.getMemberByUuid(uuid);
    const { x, y } = this._placedCardsContainer.toLocal(position, this);
    const { name } = store.game.boardModel.getMemberByUuid(uuid);
    const {
      viewConfig: { angle, placePosition }
    } = getMemberConfigByName(name);

    if (!isPlayer) {
      const cardView = this._createCard(cardModel);
      this._placedCards.push(cardView);
      cardView.position.set(x, y - 30);
      this._throwCard(cardView, angle, placePosition);
    }
  }

  _createCard(cardModel) {
    const { suit, key, uuid } = cardModel;

    const card = new CardComponent(uuid, suit, key);
    this._placedCardsContainer.addChild(card);

    return card;
  }

  _throwCard(cardView, angle, placePosition, isPlayerCard = false) {
    const { x, y } = placePosition;
    const scale = 0.55 * 1.38;

    if (isPlayerCard) {
      this.game.add
        //
        .tween(cardView.scale)
        .to({ x: scale, y: scale * 0.75 }, 600, Phaser.Easing.Cubic.InOut, true).universal = true;
    }

    const tw = this.game.add.tween(cardView).to({ x, y, angle }, 600, Phaser.Easing.Cubic.InOut, true);

    tw.universal = true;
    tw.onComplete.add(() => {
      lego.event.emit(ViewEvents.BoardView.CardTrowComplete, isPlayerCard, cardView.uuid);
    });
  }

  _createOpponents(opponentsModels) {
    return opponentsModels.map(opponentModel => {
      const { balance, avatar, name, uuid } = opponentModel;
      const config = getMemberConfigByName(name);
      const opponent = new MemberComponent(uuid, { balance, avatar, name });
      const { x, y } = config.viewConfig.position;
      opponent.position.set(x, y);
      this.addChild(opponent);
      opponent.rebuild();

      return opponent;
    });
  }

  _createPlayer(playerModel) {
    const { uuid, name } = playerModel;
    const config = getMemberConfigByName(name);
    const { x, y } = config.viewConfig.position;
    const player = new PlayerView(uuid);
    player.position.set(x, y);
    this.addChild(player);
    player.rebuild();
    player.throwCard.add(this._onPlayerThrowCard, this);

    return player;
  }

  _build() {
    this._buildTable();
    this._buildPlacedCardsContainer();
  }

  _buildTable() {
    const img = makeImage(getTableImageConfig());
    this.addChild((this._table = img));
  }

  _buildPlacedCardsContainer() {
    this._placedCardsContainer = new Container();
    this.addChild(this._placedCardsContainer);
    this._placedCardsContainer.scale.set(LP(1, 1.25));
    this._placedCardsContainer.position.set(0, LP(-130, -100));
  }
}
