/* eslint-disable max-len */
import { lego } from '@armathai/lego';
import { TutorialSequence } from '../../../constants';
import { hintModelGuard } from '../../../guards/ad/hint-model-guard';
import { store } from '../../../models/store';
import { calculateWinnerCommand } from '../../game/board/calculate-winner-command';
import { emptyPlacedCardsCommand } from '../../game/board/empty-placed-cards-command';
import { highlightSpadesCommand } from '../../game/board/member/highlight-spades-command';
import { hideHintCommand } from '../hint/hide-hint-command';
import { stopHintVisibilityTimerCommand } from '../hint/stop-hint-visibility-timer-command';

export function onTutorialSequenceShowUpdateCommand(value, prevValue, uuid) {
  const { index } = store.ad.tutorial.getSequenceByUuid(uuid);

  lego.command
    //
    .guard(() => index === TutorialSequence.SpadesTrump)
    .execute(highlightSpadesCommand)

    .guard(() => index === TutorialSequence.HighestWins)
    .execute(calculateWinnerCommand, emptyPlacedCardsCommand)

    .guard(hintModelGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand);
}
