import { store } from '../../models/store';

export function higherRankCardGuard(card) {
  const { boardModel } = store.game;
  const { placedCards } = boardModel;
  const { rank } = card;

  const { rank: highestPlacedCardRank } = boardModel.getHighestCard(placedCards);

  return rank > highestPlacedCardRank;
}
