/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { CardStackRow, CardState } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { Container } from '../../../utils/container';
import { CardComponent } from './card-component';

export class PlayerView extends Container {
  constructor(uuid) {
    super();

    this._uuid = uuid;
    this._cards = null;

    this.throwCard = new Phaser.Signal();

    lego.event.on(ModelEvents.CardModel.StateUpdate, this._onCardStateUpdate, this);
    lego.event.on(ModelEvents.CardModel.InputUpdate, this._onCardInputUpdate, this);
  }

  get name() {
    return 'PlayerView';
  }

  get uuid() {
    return this._uuid;
  }

  get firstRow() {
    return this._cards.filter(card => card.row === CardStackRow.First);
  }

  get secondRow() {
    return this._cards.filter(card => card.row === CardStackRow.Second);
  }

  getCardByUuid(uuid) {
    return this._cards.find(card => card.uuid === uuid);
  }

  rebuild() {
    const { x, y } = this.parent.scale;
    this.scale.set(0.92 / x, 0.92 / y);
    this._cards && this._updateCardsScales();
    this._cards && this._updateCardsPositions(true);
  }

  createCards(cards) {
    this._cards = cards.map(cardModel => {
      const { suit, key, uuid, row } = cardModel;
      const card = new CardComponent(uuid, suit, key, row);
      this.addChild(card);

      card.onCardClicked.add(cardUuid => {
        this._cards.forEach(c => {
          c.switchInput(false);
        });
        lego.event.emit(ViewEvents.PlayerView.CardClick, cardUuid);
      });

      return card;
    });

    this._updateCardsScales();
    this._updateCardsPositions(true);
  }

  _updateCardsScales() {
    this._cards.forEach(card => {
      card.scale.set(LP(0.9, 1));
    });
  }

  _onCardInputUpdate(input, prevValue, cardUuid) {
    this._switchCardInput(cardUuid, input);
  }

  _onCardStateUpdate(state, prevState, uuid) {
    switch (state) {
      case CardState.Block:
        this._lockCard(uuid);
        break;
      case CardState.Unblock:
        this._unlockCard(uuid);
        break;
      case CardState.Accept:
        this._placeCard(uuid);
        this._updateCardsPositions(false);
        break;
      case CardState.Reject:
        this._rejectCard(uuid);
        break;

      default:
        break;
    }
  }

  _rejectCard(uuid) {
    const card = this.getCardByUuid(uuid);
    const { y } = card;
    const tw = this.game.add.tween(card).to({ y: y - 20 }, 500, Phaser.Easing.Back.Out, true, 0, 0, true);

    tw.universal = true;
    tw.onComplete.addOnce(() => {
      lego.event.emit(ViewEvents.PlayerView.RejectComplete, uuid);
    });
  }

  _placeCard(uuid) {
    const card = this.getCardByUuid(uuid);
    const { suit } = store.game.boardModel.player.getCardByUuid(uuid);

    card.switchInput(false);
    const index = this._cards.indexOf(card);
    this._cards.splice(index, 1);

    this.throwCard.dispatch(card, suit);
    this._updateCardsPositions();
  }

  _lockCard(uuid) {
    const card = this.getCardByUuid(uuid);
    card.lock();
  }

  _unlockCard(uuid) {
    const card = this.getCardByUuid(uuid);
    card.unlock();
  }

  _switchCardInput(uuid, input) {
    const card = this.getCardByUuid(uuid);
    card.switchInput(input);
  }

  _updateCardsPositions(force) {
    if (LP(true, false)) {
      this._updateCardsLayout(this._cards, CardStackRow.First, force);
    } else {
      this._updateCardsLayout(this.firstRow, CardStackRow.First, force);
      this._updateCardsLayout(this.secondRow, CardStackRow.Second, force);
    }
  }

  _updateCardsLayout(cards, rowName, force) {
    const xGap = 45;
    const { length: cardsCount } = cards;

    cards.forEach((card, index) => {
      const startX = -((cardsCount - 1) * xGap) / 2;
      const y = rowName === CardStackRow.Second ? card.height / 2 + LP(0, 10) : LP(0, 10);
      const x = startX + index * xGap;

      if (force) {
        card.position.set(x, y);
      } else {
        card.y = y;
        this.game.add.tween(card).to({ x }, 500, Phaser.Easing.Cubic.InOut, true, index * 50).universal = true;
      }
    });
  }
}
