import { MemberState } from '../../../constants';
import { delayRunnable } from '../../../utils';
import { ObservableModel } from '../../observable-model';
import { CardModel } from './card-model';

export class MemberModel extends ObservableModel {
  constructor({ name, avatar, balance, isPlayer }) {
    super('MemberModel');

    this._name = name;
    this._avatar = avatar;
    this._balance = balance;
    this._isPlayer = isPlayer;
    this._state = MemberState.Unknown;
    this._cards = null;
    this._lastPlacedCard = null;

    this.makeObservable('_cards', '_state');
  }

  get name() {
    return this._name;
  }

  get avatar() {
    return this._avatar;
  }

  get balance() {
    return this._balance;
  }

  get isPlayer() {
    return this._isPlayer;
  }

  get state() {
    return this._state;
  }

  get cards() {
    return this._cards;
  }

  get lastPlacedCard() {
    return this._lastPlacedCard;
  }

  getCardsBySuit(suit) {
    return this._cards.filter(card => card.suit === suit);
  }

  getCardsByState(state) {
    return this._cards.filter(card => card.state === state);
  }

  getCardByUuid(uuid) {
    return this._cards.find(card => card.uuid === uuid);
  }

  getCardBySuitAndRank(suit, rank) {
    return this._cards.find(card => card.suit === suit && card.rank === rank);
  }

  selectCard(suit) {
    this._state = MemberState.SelectCard;
    delayRunnable(100, () => {
      this.placeCard(suit);
    });
  }

  placeCard(suit) {
    if (suit) {
      const cardWithTheSuit = this._cards.find(card => card.suit === suit);
      const index = this._cards.indexOf(cardWithTheSuit);
      this._cards.splice(index, 1);
      this._lastPlacedCard = cardWithTheSuit;
    } else {
      this._lastPlacedCard = this._cards.shift();
    }

    this._state = MemberState.PlaceCard;
  }

  initialize(cardsConfig) {
    this._cards = cardsConfig.map(cardConfig => new CardModel(cardConfig, this._uuid));
  }
}
