/* eslint-disable max-len */
import { lego, not } from '@armathai/lego';
import { hintModelGuard } from '../../../../guards/ad/hint-model-guard';
import { tutorialCurrentSequenceActiveGuard } from '../../../../guards/ad/tutorial-current-sequence-active-guard';
import { tutorialFollowSuitSequenceCompleteGuard } from '../../../../guards/ad/tutorial-follow-suit-sequence-complete-guard';
import { tutorialGoLowerSequenceCompleteGuard } from '../../../../guards/ad/tutorial-go-lower-sequence-complete-guard';
import { tutorialGoLowerSequenceShownGuard } from '../../../../guards/ad/tutorial-go-lower-sequence-shown-guard';
import { tutorialModelGuard } from '../../../../guards/ad/tutorial-model-guard';
import { isMemberPlayerGuard } from '../../../../guards/game/board/member/is-member-player-guard';
import { memberPlaceCardStateGuard } from '../../../../guards/game/board/member/member-place-card-state-guard';
import { memberSelectCardStateGuard } from '../../../../guards/game/board/member/member-select-card-state-guard';
import { spadesUnblockedGuard } from '../../../../guards/game/board/spades-unblocked-guard';
import { startHintVisibilityTimerCommand } from '../../../ad/hint/start-hint-visibility-timer-command';
import { stopHintVisibilityTimerCommand } from '../../../ad/hint/stop-hint-visibility-timer-command';
import { nextTutorialSequenceCommand } from '../../../ad/tutorial/next-tutorial-sequence-command';
import { showTutorialSequenceCommand } from '../../../ad/tutorial/show-tutorial-sequence-command';
import { placeCardCommand } from '../place-card-command';

export function onMemberStateUpdateCommand(state, prevState, uuid) {
  lego.command
    .payload(uuid)
    .guard(memberPlaceCardStateGuard)
    .execute(placeCardCommand)

    .payload(uuid)
    .guard(
      memberSelectCardStateGuard,
      isMemberPlayerGuard,
      tutorialModelGuard,
      not(tutorialFollowSuitSequenceCompleteGuard)
    )
    .execute(nextTutorialSequenceCommand, showTutorialSequenceCommand)

    .payload(uuid)
    .guard(
      memberSelectCardStateGuard,
      isMemberPlayerGuard,
      tutorialModelGuard,
      not(tutorialGoLowerSequenceCompleteGuard),
      spadesUnblockedGuard
    )
    .execute(nextTutorialSequenceCommand, showTutorialSequenceCommand)

    // Hint when tutorial param is true
    .payload(uuid)
    .guard(
      hintModelGuard,
      memberSelectCardStateGuard,
      isMemberPlayerGuard,
      tutorialModelGuard,
      not(tutorialCurrentSequenceActiveGuard),
      tutorialFollowSuitSequenceCompleteGuard,
      not(tutorialGoLowerSequenceShownGuard)
    )
    .execute(startHintVisibilityTimerCommand)

    // Hint when tutorial param is false
    .payload(uuid)
    .guard(hintModelGuard, memberSelectCardStateGuard, isMemberPlayerGuard, not(tutorialModelGuard))
    .execute(startHintVisibilityTimerCommand)

    .payload(uuid)
    .guard(hintModelGuard, not(memberSelectCardStateGuard))
    .execute(stopHintVisibilityTimerCommand);
}
