/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getBackgroundGridConfig } from '../../configs/grid-configs';
import { getBackgroundViewImageConfig } from '../../configs/image-configs';
import { AdStatus } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { makeImage, tweenTint } from '../../utils';

export class BackgroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onStatusUpdate, this);
    lego.event.on(ModelEvents.TutorialSequenceModel.ShowUpdate, this._onTutorialSequenceShowUpdate, this);
    lego.event.on(ModelEvents.TutorialSequenceModel.CompleteUpdate, this._onTutorialSequenceCompleteUpdate, this);
    lego.event.on(ModelEvents.CtaModel.PreVisibleUpdate, this._onCtaPreVisibleUpdate, this);
  }

  get name() {
    return 'BackgroundView';
  }

  getGridConfig() {
    return getBackgroundGridConfig();
  }

  dim() {
    tweenTint(this._bg, 0x777777, 0xffffff, 500);
  }

  brighten() {
    tweenTint(this._bg, 0xffffff, 0x777777, 500);
  }

  _onCtaPreVisibleUpdate() {
    this.brighten();
  }

  _onTutorialSequenceShowUpdate() {
    this.dim();
  }

  _onTutorialSequenceCompleteUpdate() {
    this.brighten();
  }

  _onStatusUpdate(status) {
    switch (status) {
      case AdStatus.Game:
        this._createBg('bg/game.jpg');
        break;
      default:
    }
  }

  _createBg(key) {
    this._bg && this._bg.destroy();
    this._bg = makeImage(getBackgroundViewImageConfig(key));
    this.setChild('bg', this._bg);
  }
}
